import * as React from 'react';

import { SharedDataCtx, SharedDataState } from '@miq/contextjs';
import { getClassName as getCN } from '@miq/utiljs';
import { BaseLayout, IBaseLayoutProps, Loading } from '@miq/componentjs';
import { StaffNavLink } from './components';
import { Route, Routes } from 'react-router-dom';
import SettingLayout from './Setting';
import AccountStaffLayout from './Account';

export interface IStaffBaseLayoutProps extends IBaseLayoutProps {
  sidebar?: React.ReactNode;
  mobileNav?: React.ReactNode;
  sidebarCN?: string;
  mainCN?: string;
  mobileNavCN?: string;
}

const prefix = 'miq-staff-layout';

const StaffBaseLayout: React.FC<IStaffBaseLayoutProps> = (props) => {
  const ctx: SharedDataState = React.useContext(SharedDataCtx);

  if (!ctx) return null;

  const { className, fallback = <Loading />, ...viewProps } = props;
  if (!ctx.isLoaded) return fallback;

  const { user, site } = ctx;
  if (!user || !user.is_staff) return fallback;

  const { sidebar, sidebarCN, children, mainCN, mobileNav, mobileNavCN, ...rest } = viewProps;

  return (
    <BaseLayout {...rest} className={getCN([className, prefix])}>
      <nav className={getCN([`${prefix}-sidebar`, sidebarCN])}>
        <header className={`${prefix}-sidebar-header`}>
          <div className="text-md text-ellipsis p-1">{site.name}</div>
        </header>

        <section className={`${prefix}-sidebar-body`}>
          <div className={`${prefix}-sidebar-body-content`}>{sidebar}</div>

          <div className={`${prefix}-sidebar-body-footer`}>
            <StaffNavLink to="account/">{user.first_name}</StaffNavLink>
            <StaffNavLink to="settings/">Settings</StaffNavLink>
          </div>
        </section>
      </nav>

      <div className={getCN([`${prefix}-main`, mainCN])} role="main">
        <div className="miq-container-fluid">
          <Routes>
            {children}
            <Route path="account/*" element={<AccountStaffLayout />} />
            <Route path="settings/*" element={<SettingLayout />} />
          </Routes>
        </div>
      </div>

      {mobileNav && <nav className={getCN([`${prefix}-mobile-nav`, , mobileNavCN])}>{mobileNav}</nav>}
    </BaseLayout>
  );
};

//#region StaffLayout

export default StaffBaseLayout;

//#endregion StaffLayout
